import React from 'react'

import AddInsight from './AddInsight'
import Insight from '../model/Insight'
import InsightList from './InsightList'
import LoadingBar from './LoadingBar'
import ScriptureCache from '../firebase/ScriptureCache'
import SingleInsight from './SingleInsight'
import SingleVerse from './SingleVerse'
import useFirebaseUser from '../firebase/FirebaseUser'
import VerseHighlightContext from './VerseHighlightContext'

function InsightListPage({ match: { params: { volume, book, chapter, verse: verseNumber } } }) {
  const user = useFirebaseUser()

  const [verse, setVerse] = React.useState()
  const [insights, setInsights] = React.useState()
  const [newInsight, setNewInsight] = React.useState({
    source: {
      label: '',
      link: ''
    },
    text: '',
    fragment: [0, 0]
  })
  const [adding, setAdding] = React.useState(false)
  const [highlight, setHighlight] = React.useState({ first: 0, last: 0 })

  const contextState = {
    highlight,
    updateHighlight: (f, l) => setHighlight({ first: f, last: l })
  }

  function toggleAdding() {
    setAdding(!adding)
  }

  const scriptureCache = new ScriptureCache()

  React.useEffect(() => {
    scriptureCache.getVerse(volume, book, chapter, verseNumber, setVerse)
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  React.useEffect(() => {
    scriptureCache.getInsights(volume, book, chapter, verseNumber, setInsights)
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [adding])

  return (
    <VerseHighlightContext.Provider value={contextState}>
      <div className="row">
        <div className="col-lg-6 col-md-6 col-sm-12">
          <h4 className="text-capitalize">{book.replace(/_/g, ' ')} {chapter}:{verseNumber}</h4>
          <hr/>
          <div className="sticky-top">
            {verse ? <SingleVerse verse={verse} /> : <LoadingBar />}
            <hr/>
            {!adding && verse && user.status === 'LOGGED_IN' && <button type="button" className="btn btn-warning" onClick={toggleAdding}>Add Insight</button>}
            {adding && verse ? <AddInsight verse={verse} insight={newInsight} setInsight={setNewInsight} toggle={toggleAdding} /> : null}
          </div>
        </div>
        <div className="col-lg-6 col-md-6 col-sm-12">
          {verse ? null : <LoadingBar />}
          {!adding && verse ? <InsightList verse={verse} insights={insights} toggle={toggleAdding} /> : null}
          {adding && verse ?
            <>
            <h4>Preview</h4>
            <SingleInsight verse={verse} insight={new Insight(null, verse.getVerseId(), newInsight.text, newInsight.fragment, newInsight.source)} />
            </>
           : null}
        </div>
      </div>
    </VerseHighlightContext.Provider>
  )
}

export default InsightListPage
