import React from 'react'
import { withRouter } from 'react-router-dom';
import useFirebaseUser from '../firebase/FirebaseUser'

function Footer(props) {

  const userState = useFirebaseUser()

  const href = "#/login?return=" + props.location.pathname

  let loginButton
  if (userState.status === 'LOGGED_OUT') {
    loginButton = <a className="text-muted" href={href}>Login</a>
  }

  return (
    <footer className="page-footer font-small bg-light fixed-bottom">
      <div className="container">
        <div className="footer-copyright text-center py-2">
          <span className="mr-1">© 2019 Copyright:</span>
          <a href={process.env.PUBLIC_URL}>{process.env.PUBLIC_URL.replace('https://', '')}</a>
        </div>
        <div className="text-center py-2">
          {loginButton}
        </div>
      </div>
    </footer>
  )
}

export default withRouter(Footer)
