import React from 'react'
import PropTypes from 'prop-types'

import Insight from '../model/Insight'
import LoadingBar from './LoadingBar'
import SingleInsight from './SingleInsight'
import Verse from '../model/Verse'

function compareInsightsByFragment(a, b) {
  function compareIdx(a, b) {
    return a < b ? -1 : a > b ? 1 : 0
  }

  let r = compareIdx(a.fragment[0], b.fragment[0])
  if (r === 0) {
    r = compareIdx(a.fragment[1], b.fragment[1])
  }

  return r
}

function InsightList({ verse, insights }) {

  let insightContent;
  if (insights === undefined) {
    insightContent = <LoadingBar />
  }
  else {
    insightContent = []
    // Sort insights by fragment
    insights.sort(compareInsightsByFragment)

    let currentFragment = [0, 0]
    for (let idx = 0; idx < insights.length; idx++) {
      let i = insights[idx]
      if (currentFragment[0] !== i.fragment[0] || currentFragment[1] !== i.fragment[1]) {
        currentFragment = i.fragment
        let phrase = verse.text.split(' ').slice(i.fragment[0], i.fragment[1]).join(' ')
        insightContent.push(
          <div key={i.fragment}>
            <hr/>
            <p className="text-primary font-italic">"{phrase}"</p>
          </div>
        )
      }

      insightContent.push(<SingleInsight key={i.id} verse={verse} insight={i} />)
    }
  }

  return (
    <>
      <h4 className="text-capitalize">insights</h4>
      {insightContent}
    </>
  )
}

InsightList.propTypes = {
  verse: PropTypes.instanceOf(Verse).isRequired,
  insights: PropTypes.arrayOf(PropTypes.instanceOf(Insight)),
}

export default InsightList
