import React from 'react'

import LoadingBar from './LoadingBar'
import ScriptureCache from '../firebase/ScriptureCache'

function buildButton(volume, book, chapter) {
  const classes = "text-capitalize text-white btn btn-success btn-lg"
  const href = '#/scriptures/' + volume + '/' + book + '/' + chapter

  return (
    <div key={href} className="col text-center text-nowrap p-3">
      <a href={href} role="button" className={classes}>{chapter}</a>
    </div>
  )
}

function ChapterListPage({ match: { params: { volume, book } } }) {
  const [chapterCount, setChapterCount] = React.useState(0)
  const scriptureCache = new ScriptureCache()

  React.useEffect(() => {
    scriptureCache.getChapterCount(volume, book, setChapterCount)
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  let content;
  if (chapterCount === 0) {
    content = <LoadingBar />
  }
  else {
    content = []
    for (let c = 1; c <= chapterCount; c++) {
      content.push(buildButton(volume, book, c))
    }
  }

  return (
    <div>
      <h3 className="text-capitalize">{book.replace(/_/g, ' ')}</h3>
      <div className="row">
        {content}
      </div>
    </div>
  )
}

export default ChapterListPage
