import PropTypes from 'prop-types'
import React from 'react'
import ReactMarkdown from 'react-markdown'

import Insight from '../model/Insight'
import Verse from '../model/Verse'
import VerseHighlightContext from './VerseHighlightContext'

function SingleInsight({verse, insight}) {
  const highlightContext = React.useContext(VerseHighlightContext)

  let phrase = verse.text.split(' ').slice(insight.fragment[0], insight.fragment[1]).join(' ')

  let source = insight.source.label
  if (insight.source.link !== undefined && insight.source.link !== '') {
    source = <a className="text-danger" href={insight.source.link} target="_blank" rel="noopener noreferrer">{insight.source.label}</a>
  }

  return (
    <div className="insight m-1 p-1"
      onMouseEnter={() => highlightContext.updateHighlight(insight.fragment[0], insight.fragment[1])}
      onMouseLeave={() => highlightContext.updateHighlight(0, 0)}>
      {/* TODO: react-markdown to render this? */}
      <div className="text-justify">
        <span className="text-danger pr-2">({source})</span>
        <span className="text-success pr-2">{phrase}</span>
        <ReactMarkdown source={insight.text} />
        <br/>
        {insight.created && <span className="text-secondary small float-right">{insight.created.getMonth() + 1}/{insight.created.getDate()}/{insight.created.getFullYear()}</span>}
      </div>
    </div>
  )
}

SingleInsight.propTypes = {
  verse: PropTypes.instanceOf(Verse).isRequired,
  insight: PropTypes.instanceOf(Insight).isRequired
}

export default SingleInsight
