import React from 'react'

import LoadingBar from './LoadingBar'
import ScriptureCache from '../firebase/ScriptureCache'

function buildButton(label) {
  const classes = "text-capitalize text-white btn btn-success btn-lg"
  const href = '#/scriptures/' + label

  return (
    <div key={href} className="col-sm text-center text-nowrap p-3">
      <a href={href} role="button" className={classes}>{label.replace(/_/g, ' ')}</a>
    </div>
  )
}

function VolumeListPage() {
  const [volumes, setVolumes] = React.useState([])
  const scriptureCache = new ScriptureCache()

  React.useEffect(() => {
    scriptureCache.getVolumes(setVolumes)
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  let content;
  if (volumes.length === 0) {
    content = <LoadingBar />
  }
  else {
    content = volumes.map(buildButton)
  }

  return (
    <div>
      <h1>Scriptures</h1>

      <div className="row">
        {content}
      </div>
    </div>
  )
}

export default VolumeListPage
