import React from 'react'

import LoadingBar from './LoadingBar'
import ScriptureCache from '../firebase/ScriptureCache'

function buildButton(volume, label) {
  const classes = "text-capitalize text-white btn btn-success btn-lg"
  const href = '#/scriptures/' + volume + '/' + label

  return (
    <div key={href} className="col-sm text-center text-nowrap p-3">
      <a href={href} role="button" className={classes}>{label.replace(/_/g, ' ')}</a>
    </div>
  )
}

function BookListPage({ match: { params: { volume } } }) {
  const [books, setBooks] = React.useState([])
  const scriptureCache = new ScriptureCache()

  React.useEffect(() => {
    scriptureCache.getBooks(volume, setBooks)
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  let content;
  if (books.length === 0) {
    content = <LoadingBar />
  }
  else {
    content = books.map(label => buildButton(volume, label))
  }

  return (
    <div>
      <h2 className="text-capitalize">{volume.replace(/_/g, ' ')}</h2>
      <div className="row">
        {content}
      </div>
    </div>
  )
}

export default BookListPage
