class Verse {
  constructor(volumeName, bookName, chapter, number, text) {
    this.volume = volumeName
    this.book = bookName
    this.chapter = chapter
    this.n = number
    this.text = text
  }

  getHref() {
    return ['/scriptures', this.volume, this.book, this.chapter, this.n].join('/')
  }

  getVerseId() {
    return [
      this.volume.split('_').map(s => s[0]).join(''),
      this.book.split('_').map(s => s.substring(0, 3)).join('-'),
      this.chapter,
      this.n
    ].join('/')
  }
}

export default Verse
