import { Redirect } from 'react-router-dom'
import PropTypes from 'prop-types'
import React from 'react'

import Verse from '../model/Verse'
import VerseHighlightContext from './VerseHighlightContext'

function SingleVerse({ verse, clickable }) {
  const [redirect, setRedirect] = React.useState()
  const highlightContext = React.useContext(VerseHighlightContext)

  let divStyle = "m-1 p-1 rounded "
  if (clickable) {
    divStyle += " clickable-verse"
  }

  let clickHandler;
  if (clickable) {
    clickHandler = () => setRedirect(verse.getHref())
  }

  if (redirect !== undefined) {
    return <Redirect to={redirect} />
  }

  let verseContent = verse.text
  const h = highlightContext.highlight
  if (h !== undefined && h.first !== h.last) {
    const words = verse.text.split(' ')
    verseContent = [
      <span key="before">{words.slice(0, h.first).join(' ') + ' '}</span>,
      <span key="highlighted" className="verse-highlight rounded">
        {words.slice(h.first, h.last).join(' ')}
      </span>,
      <span key="after">{' ' + words.slice(h.last).join(' ')}</span>
    ]
  }

  return (
    <div className={divStyle} onClick={clickHandler}>
      <p className="text-justify">
        <span className="font-weight-bold mr-1">{verse.n}</span>
        <span>{verseContent}</span>
      </p>
    </div>
  )
}

SingleVerse.propTypes = {
  verse: PropTypes.instanceOf(Verse).isRequired,
  clickable: PropTypes.bool
}

export default SingleVerse
