import React from 'react'
import noUiSlider from 'nouislider'

import PropTypes from 'prop-types'
import Verse from '../model/Verse'
import VerseHighlightContext from './VerseHighlightContext'
import ScriptureCache from '../firebase/ScriptureCache'
import Insight from '../model/Insight'

function AddInsight({ verse, insight, setInsight, toggle }) {
  const highlightContext = React.useContext(VerseHighlightContext)

  const sliderRef = React.createRef()

  const [isSubmitting, setSubmitting] = React.useState(false)

  const scriptureCache = new ScriptureCache()

  function handleSubmit() {
    setSubmitting(true)

    const i = new Insight(null, verse.getVerseId(), insight.text, insight.fragment, insight.source)

    scriptureCache.saveNewInsight(i).then(function () {
      setSubmitting(false);
      toggle()
    }).catch(function (err) {
      console.error('An error occurred while saving insight: ', err)
      setSubmitting(false);
    })
  }

  React.useEffect(() => {
    const lastWord = verse.text.split(' ').length + 1

    const { current: sliderNode } = sliderRef

    noUiSlider.create(sliderNode, {
      start: [1, lastWord],
      connect: true,
      range: {
        'min': 1,
        'max': lastWord
      },
      step: 1
    })

    sliderNode.noUiSlider.on('update', function (values, handle) {
      setInsight({...insight, fragment: [values[0] - 1, values[1] - 1]})
      highlightContext.updateHighlight(values[0] - 1, values[1] - 1)
    })

    return () => sliderNode.noUiSlider.destroy()
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  return (
    <div>
      <h4>New Insight</h4>
      <form onSubmit={handleSubmit}>
        <div className="form-group">
          <label htmlFor="source-label">Source Label</label>
          <input type="text" id="source-label" className="form-control" required
            value={insight.source.label}
            onChange={event => setInsight({...insight, source: {...insight.source, label: event.target.value}})}/>
        </div>

        <div className="form-group">
          <label htmlFor="source-link">Source Link</label>
          <input type="url" id="source-link" className="form-control"
            value={insight.source.link}
            onChange={event => setInsight({...insight, source: {...insight.source, link: event.target.value}})}/>
        </div>

        <div className="form-group">
          <label htmlFor="phrase">Phrase Highlight</label>
          <div ref={sliderRef} />
        </div>

        <div className="form-group">
          <label htmlFor="insight-text">Insight</label>
          <textarea id="insight-text" className="small form-control" rows="10" required
            value={insight.text} onChange={event => setInsight({...insight, text: event.target.value})} />
        </div>

        <button type="button" className="btn btn-danger mr-3" onClick={toggle} disabled={isSubmitting}>Cancel</button>
        <button type="submit" className="btn btn-success" disabled={isSubmitting}>Save</button>
      </form>
    </div>
  )
}

AddInsight.propTypes = {
  verse: PropTypes.instanceOf(Verse).isRequired,
  insight: PropTypes.object.isRequired,
  setInsight: PropTypes.func.isRequired,
  toggle: PropTypes.func.isRequired
}

export default AddInsight
