import React from 'react'

import LoadingBar from './LoadingBar'
import ScriptureCache from '../firebase/ScriptureCache'
import SingleVerse from './SingleVerse'

function ChapterTextPage({ match: { params: { volume, book, chapter } } }) {
  const [verses, setVerses] = React.useState([])
  const scriptureCache = new ScriptureCache()

  React.useEffect(() => {
    scriptureCache.getVerses(volume, book, chapter, setVerses)
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  let content;
  if (verses.length === 0) {
    content = <LoadingBar />
  }
  else {
    content = verses.map(v => <SingleVerse key={v.getHref()} verse={v} clickable={true}/>)
  }

  return (
    <div>
      <h4 className="text-capitalize">{book.replace(/_/g, ' ')} {chapter}</h4>
      <div className="row">
        <div className="col-3"></div>
        <div className="col-sm-6">{content}</div>
        <div className="col-3"></div>
      </div>
    </div>
  )
}

export default ChapterTextPage
