import React from 'react'

function toBreadcrumb(segment) {
  return segment.replace(/_/g, ' ')
}

function Breadcrumb(props) {
  const segments = ['home'].concat(props.location.pathname.substring(1).split('/').filter(seg => seg !== ""))
  let href = '#'
  const crumbs = segments.map(function (s, i) {
    let last = i === segments.length - 1

    if (i > 0) {
      href += '/' + s
    }

    let label = <a href={href}>{toBreadcrumb(s)}</a>

    let classNames = "breadcrumb-item text-capitalize"
    if (last) {
      classNames += " active"
      label = toBreadcrumb(s)
    }

    return (
      <li key={href} className={classNames}>
        {label}
      </li>
    )
  })

  return (
    <div style={{ backgroundColor: '#e9ecef' }}>
      <div className="container">
        <ol className="breadcrumb" style={{ marginBottom: '0' }}>
          {crumbs}
        </ol>
      </div>
    </div>
  )
}

export default Breadcrumb
