import React from 'react';

function Background() {
  const style = {
    position: 'fixed',
    top: 0,
    zIndex: -1,
    width: '100%',
    height: '100%',
    backgroundPosition: 'center',
    backgroundRepeat: 'no-repeat',
    backgroundSize: 'cover',
    opacity: '0.9'
  }

  return (
    <img alt="A pathway with trees on either side in autumn" src="/background.jpg" style={style} />
  )
}

export default Background
